import React, { Component } from 'react';

class Alert extends Component {
  constructor(props) {
    super(props);
    this.color = null;
  }

  getStyle = () => {
    return {
      color: this.color,
    };
  };

  render() {
    return (
      <div className='Alert'>
        <p style={this.getStyle()}>{this.props.text}</p>
      </div>
    );
  }
}

class InfoAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = '#323edd';
  }
}

class WarningAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = '#FFA500';
  }
}

class ErrorAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = '#ec0101';
  }
}

export { InfoAlert, WarningAlert, ErrorAlert };
